@use '../utils/vars';
@use '../utils/mixins' as *;

.button {
  &--contained {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;

    height: 60px;

    font-family: $font-family;
    font-weight: $button-text-weight;
    font-size: $button-text-size;
    line-height: $button-text-line-height;
    letter-spacing: $button-text-letter-spacing;
    color: $color4;
    text-decoration: none;
    text-transform: uppercase;

    background-color: $color1;
    transition: $page-transition;

    &:hover {
      border: 1px solid $color1;
      color: $color1;

      background-color: $bg-color;
      transition: $page-transition;
    }
  }

  &--underlined {
    display: inline-block;
    width: max-content;
    padding-bottom: 10px;

    font-family: $font-family;
    font-weight: $decorative-text-weight;
    font-size: $decorative-text-size;
    line-height: $decorative-text-line-height;
    letter-spacing: $decoratice-text-letter-spacing;
    text-decoration: none;
    color: $color6;

    border-bottom: 1px solid $bg-color;
    transition: $page-transition;

    &:hover {
      border-bottom: 1px solid $color6;
      transition: $page-transition;
    }
  }

  // &--type3 {
